import * as PIXI from "pixi.js";
import noise from "./noise.jpg";
import img from "./img/img0";

class Sketch{
    constructor() {
        this.width = window.innerWidth;
        this.height = window.innerHeight;

        //(PIXI.Application): Convenience class to create a new PixiJS application.
        //This class automatically creates the renderer, ticker and root container.
        this.app = new PIXI.Application({
            backgroundColor: 0x1099bb,
            resolution: 1,

            resizeTo: window
            //The HTML element or window to automatically resize the renderer's view element to match width and height.

    });

    //Reference to the renderer's canvas element.
    document.body.appendChild(this.app.view);
    this.app.view.style.width = this.width + "px"
    this.app.ciew.style.height = this.height + "px"


    //Container is a general-purpose display object that holds children. It also adds built-in 
    //support for advanced rendering features like masking and filtering.
    // It is the base class of all display objects that act as a container for other objects, including Graphics and Sprite.
    this.container = new PIXI.Container();

    //(stage): The root display container that's rendered.
    this.app.stage.addChild(this.container);

    this.add() //graphics
    this.resize();
    this.setupResize();
    this.render();

    }
    
    add() {
        //Sprite object is the base for all textured objects that are rendered to the screen
        let block = new PIXI.Sprite(PIXI.Texture.WHITE);

        //tint applied to the sprit
        block.tint = 0xff0000;
        block.width = 100;
        block.height = 100;

        this.container.addChild(block);

        //in Pixi you can create texture right from the image... 

        let image = new PIXI.Sprite.from(img);
    }

    //Dont completely understand this
    setupResize() {
        window.addEventListener("resize", this.resize.bind(this));
    }

    resize() {
        this.windth = window.innerWidth;
        this.height = window.innerHeight;
        this.quad.scale.set(this.width/200, this.height/200)
        this.quad.position.set(this.width/2, this.height/2);
        this.app.view.style.width = this.width + "px"


    }
}